:root {
    --backgroundColor: rgba(0, 0, 0, 0.24);
    --font-color: white;
  }
  
  .baseball-card {
    border: 2px solid #000;
    border-radius: 10px;
    padding: 10px;
    margin: 10px;
    width: 300px; 
    height: 520px;
    background-color: var( --backgroundColor);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .player-info {
    text-align: center;
    font-size: 20px;
  }
  
  .player-name {
    color: var(--font-color);
    font-size: 30px;
    margin-bottom: 5px;
  }
  
  .player-img {
    max-width: 120px; 
    max-height: 120px; 
    object-fit: cover; 
    border-radius: 8px; 
    margin-bottom: 10px; 
  }

  .player-fav-car {
    
    font-style: italic;
    color: var(--font-color);
  }
  
  .player-stats {
    color: var(--font-color);
    margin-top: 10px;
  }
  
  .player-stats p {
    color: var(--font-color);
    margin: 5px 0;
  }