.charts-selection {
    display: flex;
    background-color: rgb(87, 87, 87);
    border: 10px solid #252525;
    height: 500px;
    width: 900px;
    padding: 10px;
  }

  .left-area {
    display: flex;
    flex-direction: column;
    flex: 1; 
  }
  
  .chart-selector {
    display: flex;
    flex-direction: column;
    
  }
  
  .chart-selector label {
    font-weight: bold;
  }
  
  .right-chart {
    flex: 2; 
    margin-left: 10px; 
  }
  
  .chart-selector select {
    padding: 5px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  .right-chart {
    box-sizing: border-box;
    border: 10px solid #252525;
    width: 100%;
    max-width: 500px; 
    height: auto;
  }
  
  .stat-options {
    background-color: rgb(46, 46, 46);
    border: 3px solid rgb(41, 41, 41);
    display: flex;
    flex-direction: column;
  }
